@import "./particals/colors";
@import "./particals/font";
@import "./particals/framework";
@import "./particals/loader";
@import './particals/inputElements';

#app_wrap {
    --aside_width: 320px;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: var(--aside_width) 1fr var(--aside_width);
    grid-template-rows: 80px 1fr;
    header {
        grid-area: 1/2/2/4;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 2%;
        padding-left: 80px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
        form{
            width: 40%;
            min-width: 400px;
            .input_section[data-inputElement='search_bar']{
          
                #search_bar{
                    height: 45px;
                    border-radius:10px;
                }
      
              }
        }
        .profile_header_section{
            .profile_icon{
                width: 50px;
                height: 50px;
                border-radius: 50%;
                background-image: url(../../images/avatar/man_avatar_dark.svg);
                background-size: cover;
            }
        }
    
        
    }
    #nav_bar {
        position: relative;
        grid-area: 1/1/3/2;
        padding: 1rem;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
        background-color: var(--white_color);
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
        margin-bottom: 2rem;
        .size_toggle_btn {
            cursor: pointer;
            position: absolute;
            padding: 4px;
            border-radius: 50%;
            right: -15px;
            top: 25px;;
            border: none;
            background-color: var(--white_color);
            box-shadow: 0px 15px 32px rgba(0, 0, 0, 0.08), 0px 12px 24px rgba(0, 0, 0, 0.04),
                0px 4px 8px rgba(0, 0, 0, 0.02), 0px 0px 0.5px rgba(0, 0, 0, 0.04);
        }
        .app_icon_con img {
            width: 45px;
        }
        .app_icon_con{
            margin-bottom: 2rem;            
        }
        nav {
            flex-direction: column;
            .nav_item {
                cursor: pointer;
                padding: 10px;
                border-radius: 10px;
                margin-bottom: 12px;
                width: 100%;
                transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);

                .nav_icon svg {
                    width: 22px;
                    height: 22px;
                    stroke: var(--secondary_text);
                }
                .nav_text {
                    margin-left: 16px;
                    font-weight: 700;
                    color: var(--secondary_text);
                }

                &[data-state="active"] {
                    background-color: var(--grey200);
                    .nav_icon svg {
                        stroke: var(--primary_text);
                    }
                    .nav_text {
                        color: var(--primary_text);
                    }
                }
                &:hover {
                    background-color: var(--grey200);
                    .nav_icon svg {
                        stroke: var(--primary_text);
                    }
                    .nav_text {
                        color: var(--primary_text);
                    }
                }
            }
        }
        .file_upload_con{
            width: 95%;
            height: 170px;
            border: var(--outline) 3px dashed;
            border-radius: 10px;
            justify-content: center;
            flex-direction: column;
            .add_icon{
                width: 50px;
                height: 50px;
                background-color: var(--blue_color200);
                border-radius: 50%;
                justify-content: center;
                border: var(--blue_color500) 2px solid;
                margin-bottom: 20px;

                 svg{
                     stroke: var(--white_color);
                 }
            }
            h2{
                @include body_text;
                font-weight: 700;
                line-height: 20px;
            }
            p{
                @include caption_text;
                font-weight: 500;
                color: var(--secondary_text);
            }
        }
        &[data-state="minimized"] {
            padding: none;
            .size_toggle_btn {
                right: -50px;
                transform: rotate(180deg);

            }
            nav {  
                .nav_item{
                    width: auto;
                    height: auto;
                } 
                .nav_icon{
                    width: 22px;
                    height: 22px;
                }
                .nav_text {
                    display: none;
                }
            }
            .file_upload_con{
                width: 100%;
                border:none;
                .add_icon{
                    width: 40px;
                    height: 40px;
                }
                h2{
                    display: none
                }
                p{
                    display: none;
                }
            }
        }
    }
    #dashboard_section {
        grid-area: 2/2/3/3;
        width: 100%;
        height: 100%;
        background-color: var(--grey);
    }
    #user_section {
        grid-area: 2/3/3/4;
        min-width: var(--aside_width);
        height: 100%;
        padding:1rem;
        background-color: var(--grey);

        .user_section_wrap{
            padding: 1rem;
            border-radius: 10px;
            width: 100%;
            box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
            background-color: var(--white_color);
            .data_section{
                width: 100%;
                margin-bottom: 45px;
                h2{
                    @include body_text;
                    color: var(--blue_color300);
                    margin-bottom: 1rem;
                    .data_used,.total_data{
                        @include title_text;
                        color: var(--blue_color300);
                    }
                }
                .data_bar_con{
                    width: 100%;
                    height: 13px;
                    border-radius: 2px;
                    background-color:var( --blue_color100);
                    .data_bar_item{
                        height: 100%;
                        border-radius: 2px;
                    }
                    .data_bar_item:not(:first-child){
                        margin-left: -2px;
                    }
                    .data_bar_item:nth-child(1){
                        width: 20%;
                        background-color:var(--yellow_color);
                    }
    
                    .data_bar_item:nth-child(2){
                        width: 15%;
                        background-color:var(--red_color);
                    }
                    .data_bar_item:nth-child(3){
                        width: 10%;
                        background-color:var(--purple_color);
                    }
                    
                }
                
            }
            .files_section{
                width: 100%;
                .file_item{
                    width: 100%;
                    margin-bottom: 1.5rem;
                    .file_icon{
                        width: 35px;
                        height: 35px;
                        border-radius: 5px;
                        margin-right: 1rem
                    }
                    .file_name{
                        @include caption_text;
                        font-weight: 700;
                        color:var(--blue_color300)
                    }
                    .num_of_files{
                        @include tiny_text;
                        font-weight: 500;
                        color: var(--secondary_text);
                    }
                    .file_size{
                        @include subtitle_text;
                        font-weight: 600;
                        margin-left: auto;
                    }
    
                }
            }
        }
      
    }
}
